import React, { useState, useEffect, Component } from "react";
import "@trendmicro/react-sidenav/dist/react-sidenav.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "../App.css";
import "react-tabs/style/react-tabs.css";
import { Link, Redirect, withRouter } from "react-router-dom";
import AppLayout from "../components/layouts/app";
import { storePageNumber, setRegistered, clear, set as setProducts2 } from "../state/actions/index";
import { connect, useDispatch } from "react-redux";
import { refreshLocalOffers } from "../helpers/api";
import { CardWinning, CardLosing, CardTotal, CardTotalActive } from "./cards/CounterCards";
import { getGroupedOffers } from "../helpers/api";
import ProductsTable from "../components/tables/productTable";

let mapStateToProps = (state) => {
    return {
        registered: state.global.registered,
        showModal: state.global.showModal,
        products: state.products,
        // filteredProducts: state.products.map(product => {
        //     return Object.assign([], product)
        // })
    };
};

function mapDispatchToProps(dispatch) {
    return {
        storePageNumber: (pageNumber) => dispatch(storePageNumber(pageNumber)),
        setRegistered: (data) => dispatch(setRegistered(data)),
    };
}

const Products = (props) => {
    const [redirect, setRedirect] = useState(false);
    const [route, setRoute] = useState("");
    const [filter, setFilter] = useState(null);

    const dispatch = useDispatch();

    const refreshResults = () => {
        setTimeout(function () {
            refreshLocalOffers().then((products) => {
                dispatch(setProducts2(products));
            });
        }, 1000);
    };

    const showWinning = () => {
        setFilter("winning");
    };

    const showLosing = () => {
        setFilter("losing");
    };

    const showAll = () => {
        setFilter(null);
    };

    const showAllActive = () => {
        setFilter("active");
    };

    const showAllDisabled = () => {
        setFilter("active");
    };

    const redir = (newRoute) => {
        // if (window.location.pathname !== newRoute) {
        //     setRoute(newRoute);
        //     setRedirect(true);
        // }
    };

    if (redirect) return <Redirect to={route} push />;

    return (
        <AppLayout>
            <div className="row justify-content-center" style={{ paddingTop: "30px" }}>
                <div className="col-10">
                    <div className="py-4">
                        <div class="card-deck">
                            <div
                                class="card border-0"
                                onClick={() => {
                                    showAll();
                                }}
                            >
                                <CardTotal products={props.products} />
                            </div>
                            <div
                                class="card border-0"
                                onClick={() => {
                                    showAllActive();
                                }}
                            >
                                <CardTotalActive products={props.products} />
                            </div>
                            <div
                                class="card border-0"
                                onClick={() => {
                                    showWinning();
                                }}
                            >
                                <CardWinning products={props.products} />
                            </div>
                            <div
                                class="card border-0"
                                onClick={() => {
                                    showLosing();
                                }}
                            >
                                <CardLosing products={props.products} />
                            </div>
                        </div>
                    </div>
                    <div className="shadow rounded p-5 mb-5">
                        <ProductsTable products={props.products} filter={filter} didSelectItem={(product) => redir(`/product/${product.offerId}`)} />
                    </div>
                </div>
            </div>
            <div style={{ height: "50px" }}></div>
        </AppLayout>
    );
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Products));
